import React, { useEffect, useState } from 'react'
import { Redirect } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { getPoList } from 'Page/StockEntry/GeneralServices/moduleServices'
import SelectedSKUList from 'Page/StockEntry/Movement/WeightEntry/SelectedSKUList'
import WeightEntryList from 'Page/StockEntry/Movement/WeightEntry/WeightEntryList'

const WeightEntry = () => {
    const dispatch = useDispatch()
    const stockMovementEntryData = useSelector(state => state.stockMovementEntryData)
    const [hideSKUList, setHideSKUList] = useState(false)
    const [activeSku, setActiveSKU] = useState(0)
    const [updateEntry, setUpdateEntry] = useState()
    let height = document.documentElement.clientHeight
    let clientWidth = document.documentElement.clientWidth
    let heightAdj = height - 150
    const moduleName = 'stockMovement'

    useEffect(_ => {
        if (stockMovementEntryData?.jobPost.list.length > 0) getPoList({ dispatch, moduleName, moduleEntry: stockMovementEntryData, activeSku })
    }, [activeSku])

    if (!stockMovementEntryData || stockMovementEntryData?.jobPost.list.length === 0) {
        return (
            <div className='d-flex align-items-center justify-content-center text-center w-100 h-screen'><Redirect to='/inventory-entry/movement'>Please Select At Least 1 SKU</Redirect></div >)
    }
    return (
        <div style={{ height: clientWidth <= 500 ? height : heightAdj }} className='w-100 d-md-flex justify-content-between scroll-y z-0'>
            <SelectedSKUList
                hideSKUList={hideSKUList}
                activeSku={activeSku}
                setActiveSKU={setActiveSKU}
                moduleName={moduleName}
                moduleEntry={stockMovementEntryData}
                setUpdateEntry={setUpdateEntry}
            />
            <WeightEntryList
                setActiveSKU={setActiveSKU}
                setHideSKUList={setHideSKUList}
                hideSKUList={hideSKUList}
                moduleName={moduleName}
                moduleEntry={stockMovementEntryData}
                activeSku={activeSku}
                setUpdateEntry={setUpdateEntry}
                updateEntry={updateEntry}
            />
        </div>
    )
}

export default WeightEntry